.App {
  text-align: center;
}

.searchBar {
  float: right;
  height: 50px;
}

$grid__bp-md: 768;

.container {
  /*
  max-width: $grid__bp-md * 1px;
  */
  max-width: auto;
  margin: 0 auto;
  &--fluid {
    margin: 0;
    max-width: 100%;
  }
  &__row {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
  }
  &__col {
    flex-basis: (100 / 3) * 1%;
  }
}
.center {
  max-height: 400px;
  overflow-y: scroll;
  /* https://www.npmjs.com/package/react-custom-scrollbars
  maybe get this for scrollbar styling? */
}
.tasklist {
  min-width: 98%;
  max-width: 33%;
  height: 100%;
  margin-top: 40px;
  display: flex;
  flex-direction: column;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
